
import { defineComponent, reactive, toRefs } from 'vue'
import {ElMessage} from "element-plus";
export default defineComponent({
  created() {
    this.getKvs()
  },
  setup() {
    const axios = require('axios');

    const state = reactive({
      tableData: [],
      dialogFormVisible: false,
      form: {
        kv_name: '',
        kv_key: '',
        kv_value: '',
        kv_status: true,
      },
      formLabelWidth: '120px',
      dialogCommitTitle: '',
    })

    // / 
    const addOrUpdateKv = () => {
      let reg = /^[A-Za-z0-9_-]+$/
      if(state.form.kv_key.length <= 0 || !reg.test(state.form.kv_key)){
        ElMessage({
                showClose: true,
                message: "kv_key  -_",
                type: 'error',
              })
        return
      }
      if(state.form.kv_value.length <= 0){
        ElMessage({
                showClose: true,
                message: "",
                type: 'error',
              })
        return
      }
      state.dialogFormVisible = false
      console.log(JSON.stringify(state.form))
      axios.post('http://127.0.0.1:8000/api/add_kv', JSON.stringify(state.form))
          .then((response:any) => {
            const res = response.data;
            state.tableData = res.data  // 
            let msgType: any = res.code === 200? 'success' : 'error'
            ElMessage({
                showClose: true,
                message: res.msg,
                type: msgType,
              })
            console.log(res)
          })
    }

    const getKvs = () => {
      axios.get('http://127.0.0.1:8000/api/get_kvs')
          .then((response:any) => {
            const res = response.data;
            if (res.code === 200) {
              ElMessage({
                showClose: true,
                message: 'getKvs Succeed.',
                type: 'success',
              })

              // 
              state.tableData = res.data
            } else {
              ElMessage({
                showClose: true,
                message: 'getKvs Fail' + res.msg,
                type: 'error',
              })
            }
            console.log(res)
          })
    }

    // 
    const editKv = (index: any, row: any) => {
      console.log(index, row)
      state.form = {
        kv_name: row.kv_name,
        kv_key: row.kv_key,
        kv_value: row.kv_value,
        kv_status: row.kv_status
      }
      state.dialogCommitTitle = ""
      state.dialogFormVisible = true
    }

    // 
    const deleteKv = (index: any, row: any) => {
       console.log(row)
       axios.post('http://127.0.0.1:8000/api/del_kv', JSON.stringify(row))
          .then((response:any) => {
            const res = response.data;
            state.tableData = res.data  // 
            let msgType: any = res.code === 200? 'success' : 'error'
            ElMessage({
                showClose: true,
                message: res.msg,
                type: msgType,
              })
            console.log(res)
          })
    }

    // master
    const setKvStatus = (index: any, row: any) => {
      console.log(row)
       axios.post('http://127.0.0.1:8000/api/kv_status', JSON.stringify(row))
          .then((response:any) => {
            const res = response.data;
            state.tableData = res.data  // 
            let msgType: any = res.code === 200? 'success' : 'error'
            ElMessage({
                showClose: true,
                message: res.msg,
                type: msgType,
              })
            console.log(res)
          })
    }

    return {
      ...toRefs(state),
      addOrUpdateKv,
      getKvs,
      editKv,
      setKvStatus,
      deleteKv,
    }
  },
})

